html {
  font-size: $base-fz + px;
  line-height: 1.375;
  font-weight: 700;
  color: $c-text;
  @include reg;
  @include sm {
    font-size: $base-fz-sm + px; } }

body {
  background-color: $white;
  min-width: 320px;
  @include no-scroll; }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  $this: &;
  &--loaded:not(#{$this}--no-fade-in) {
    animation: fadeIn 1s; } }

.quote {
  color: $c-text;
  font-size: rem(336);
  width: em(754, 336);
  height: 1em;

  padding: em(55, 336) em(55, 336) em(110, 336);

  @include image('Union.svg');
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;

  &__inner {
    font-size: rem(30); } }


.section-block {
  @include notlast {
    margin-bottom: rem(15); }

  &__title {
    margin-bottom: rem(16); }

  &__text {
    font-weight: 400; } }

.section-btn {
  color: $white;
  text-align: center; }

