.nav {
  text-align: center;
  color: $dark-aux;

  li {
    display: inline-block;
    margin-left: rem(20);
    margin-right: rem(20); }

  a {
    display: inline-block;
    position: relative;

    padding: 0 rem(10) rem(23);
    @include before {
      position: absolute;

      bottom: 0;
      left: 0;
      right: 0;
      height: rem(14);

      background-color: currentColor;

      @include tr(opacity .3s, visibility .3s);
      @include hidden; }

    @include tr(color .3s);
    @include hover {
      color: $accent;
      @include before {
        @include visible; } } }

  &__current-item {
    color: $accent;
    a {
      @include before {
        @include visible; } } } }
